<template>
  <div class="itemBox">
    <p class="title">空白占位</p>
    <el-form ref="form" :model="itemData" size="mini">
      <el-form-item label="空白高度" class="marginType">
        <ul>
          <li>
            <span class="marginTypeName">占位高度</span>
            <el-slider v-model="itemData.height" show-input :max="100" input-size="mini"></el-slider>
          </li>
        </ul>
      </el-form-item>
      <el-form-item label="背景颜色">
        <el-radio-group class="bgColor" v-model="itemData.bgSelect" @change="bgChange">
          <el-radio :label="0" class="custom">无底色</el-radio>
          <el-radio :label="1" class="custom">
            <span class="custom">
              <span>自定义背景色</span>
              <el-color-picker
                v-show="itemData.bgSelect==1"
                v-model="itemData.backgroundColor"
                :predefine="predefineColors"
              ></el-color-picker>
              <el-button type="text" v-show="itemData.bgSelect==1" @click="itemData.backgroundColor='#fff'">重置</el-button>
            </span>
          </el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>

    <!-- {{itemData}} -->
  </div>
</template>

<script>
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      predefineColors: [
        "#ff4500",
        "#ff8c00",
        "#ffd700",
        "#90ee90",
        "#00ced1",
        "#1e90ff",
        "#c71585"
      ]
    };
  },
  methods: {
    //背景色切换
    bgChange(){
      if (this.itemData.bgSelect == 0) {
        this.itemData.backgroundColor = "#fff";
      }
    }
  }
};
</script>

<style scoped lang="less">
.active {
  border: 1px solid #000;
}
.title {
  border-bottom: 1px solid #eee;
  padding: 20px 18px 24px 18px;
  color: #333;
  font-size: 16px;
  font-weight: 700;
}
.itemBox /deep/ .el-form {
  margin: 18px 24px;
  .el-form-item__label {
    display: block;
    float: inherit;
    text-align: inherit;
    padding: 0;
    color: #222;
    font-weight: 100;
    margin-bottom: 14px;
  }
  .bgColor {
    display: flex;
    flex-direction: column;
    .el-radio {
      margin: 0 0 0 10px;
      height: 32px;
      width: 104px;
    }
  }
  .el-input__inner {
    height: 34px;
    border-radius: 2px;
  }
  .el-form-item__error {
    padding-top: 5px;
  }
  .el-radio__label {
    font-size: 12px;
    color: #333;
  }
  .el-radio {
    display: block;
    margin: 0;
    margin-left: 10px;
    margin-bottom: 12px;
  }
  .custom {
    display: flex;
    align-items: center;
    .el-color-picker {
      margin-left: 16px;
      margin-right: 10px;
    }
  }
  .marginType {
    border-bottom: 1px solid #eee;
    padding-bottom: 24px;
    li {
      position: relative;
      .el-slider {
        margin-left: 24px;
      }
      .marginTypeName {
        font-size: 12px;
        color: rgba(153, 153, 153, 1);
        margin-left: 3px;
        position: absolute;
        top: 5px;
      }
      .el-input__inner {
        height: 28px;
      }
      .el-slider__input {
        width: 92px;
      }
      .el-slider__runway.show-input {
        margin-right: 118px;
        margin-left: 50px;
      }
    }
  }
}
</style>
